/**
 * 配置编译环境和线上环境之间的切换
 * 
 * baseUrl: 域名地址
 * routerMode: 路由模式
 * imgBaseUrl: 图片所在域名地址
 * 
 */

let baseUrl = ''; 
let routerMode = 'hash';
// let websocketUrl='ws://39.108.73.183:1011'
let websocketUrl = 'ws://api.lepayedu.com:1011';

let user={
    personId: window.app.getCurrentCardId(),
    uid: JSON.parse(window.app.publicParameters()).uid,
    token:JSON.parse(window.app.publicParameters()).token
    // personId: 7288,
    // uid: 95,
    // token:"196BD53E-B470-39AA-8CF3-0B0F3AE516E8"
};

if (process.env.NODE_ENV == 'development') {
  // baseUrl='http://192.168.31.77:80'
    baseUrl='http://171.221.228.25:9200'
  // baseUrl='http://api.lepayedu.com'
}else if(process.env.NODE_ENV == 'production'){
  // baseUrl='http://192.168.31.179:8080'
  // baseUrl='http://171.221.228.25:9200'
  baseUrl='http://api.lepayedu.com'
}

export {
	baseUrl,
	routerMode,
	user,
  websocketUrl
}


